import React from "react";
import { createGlobalStyle } from 'styled-components';

/**
 * 定制中文单双引号字体，by designer
 */
const ZhQuoteFontFace = createGlobalStyle`
  @font-face {
    font-family: 'zh quote';
    src: local("PingFang SC Regular"),local("PingFang SC"),local("dengxian");
    unicode-range: U+2018,U+2019,U+201c,U+201d;
  }
`;

export default ZhQuoteFontFace;
